<template>
  <div class="container">
    <IndexHeader>WIFI 二维码生成</IndexHeader>
    <QrEditor :qrText="qrText">
      <template #contentTitle>输入内容，开始制作二维码</template>
      <template #tips>功能描述：将 wifi 信息转为二维码。</template>
      <template #help>
        <p><span>1. 有效期：</span>本页面为【wifi 静态二维码】，生成后永久有效。</p>
        <p>
          <span>2. wifi二维码原理</span>：将您输入的无线 wifi
          信息存储在二维码中，使用手机扫描二维码即可连接至该 wifi。
        </p>
        <p>
          <span
            >注意：本页面的 wifi
            二维码是静态的，需使用手机自带的“扫一扫”功能扫码才能连接，微信扫描无效。</span
          >
        </p>
      </template>
      <template #content>
        <div class="wifi-form">
          <a-input v-model:value="ssid" addon-before="WIFI 名称" placeholder="WIFI 名称（SSID）" allowClear/>
          <a-input
            v-model:value="pass"
            addon-before="WIFI 密码"
            placeholder="WIFI 密码"
            :disabled="passType === 'nopass'"
            allowClear
          />
          <a-form-item label="WIFI 加密方式" :labelCol="{ span: 3 }" style="margin-bottom: 8px">
            <a-radio-group name="encType" v-model:value="passType">
              <a-radio value="nopass">无密码</a-radio>
              <a-radio value="WPA">WPA/WPA2-PSK（常用）</a-radio>
              <a-radio value="WEP">WEP</a-radio>
            </a-radio-group>
          </a-form-item>
        </div>
      </template>
    </QrEditor>
  </div>
</template>

<script>
import IndexHeader from '@/components/IndexHeader.vue';
import QrEditor from '@/components/QrEditor/QrEditor.vue';
import { computed, reactive, toRefs } from 'vue';
import { useHead } from '@vueuse/head';

export default {
  name: 'Wifi',
  components: {
    IndexHeader,
    QrEditor
  },
  setup() {
    useHead({
      title: 'WIFI二维码生成 - 兔子二维码',
      meta: [
        { name: 'keywords', content: '二维码,WIFI,WIFI二维码' },
        {
          name: 'description',
          content: '将 wifi 信息转为二维码'
        }
      ]
    });

    let view = reactive({
      ssid: '',
      pass: '',
      passType: 'WPA'
    });

    let qrText = computed(() => {
      let content = '';
      if (view.ssid) content += `S:${view.ssid};`;
      if (content) content += `T:${view.passType};`;
      if (content && view.passType != 'nopass') content += `P:${view.pass};`;
      if (content) content = 'WIFI:' + content;
      return content || '';
    });

    return {
      ...toRefs(view),
      qrText
    };
  }
};
</script>

<style lang="less" scoped>
.wifi-form {
  display: flex;
  flex-direction: column;
  span {
    width: 600px;
    margin-bottom: 10px;
  }
}
</style>
